<template>
  <el-row type="flex" align="center" class="search">
    <el-col :span="1"> </el-col>
    <el-col :span="12" :offset="3" class="hot-lable" />
    <el-col :span="2">
      <el-select style="width:80px;" v-model="select" placeholder="请选择">
        <el-option label="景点" :value="1"> </el-option>
        <el-option label="商品" :value="2"> </el-option>
      </el-select>
    </el-col>
    <el-col :span="6">
      <el-form @submit.native.prevent :model="searchform" :rules="rules" ref="searchformref" label-width="0" class="demo-ruleForm">
        <el-form-item prop="search">
          <el-input placeholder="请输入搜索关键词" v-model="searchform.search" class="search-input" @keyup.enter.native="sub">
            <el-button slot="append" icon="el-icon-search" @click="sub" />
          </el-input>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>
<script>
export default {
  data() {
    return {
      select: 1,
      searchform: {},
      rules: {
        search: [{ required: true, message: "关键词不能为空", trigger: "blur" }]
      }
    }
  },
  methods: {
    sub() {
      this.$refs.searchformref.validate(isvalidate => {
        if (!isvalidate) return false
        this.$router.push("/search/" + this.select + "/" + this.searchform.search)
      })
    }
  }
}
</script>
<style lang="less" scoped>
// 搜索栏
.search {
  height: 60px;
  margin: 0;
  > .el-col {
    display: flex;
    // align-items: center;
    > .el-input__inner {
      height: 50px;
    }
  }
  > .hot-lable {
    > ul {
      padding: 0;
      margin: 0;
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      overflow: hidden;
      list-style: none;
      > a {
        color: #000;
        font-size: 16px;
        li {
          float: left;
          padding: 0 10px;
        }
      }
      a:hover {
        color: #0492fb;
        text-decoration: none;
      }
    }
  }
  .el-form-item {
    margin-bottom: 0;
  }
}
</style>
